.App {
  text-align: center;

    &-logo {
        animation: App-logo-spin infinite 20s linear;
        height: 90px;
    }

    &-header {
        background-color: #222;
        height: 150px;
        padding: 20px;
        color: white;
    }

    &-intro {
        font-size: large; 
    }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
