.App {
  text-align: center;
}

    .App-logo {
        animation: App-logo-spin infinite 20s linear;
        height: 90px;
    }

    .App-header {
        background-color: #222;
        height: 150px;
        padding: 20px;
        color: white;
    }

    .App-intro {
        font-size: large; 
    }

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
